.database-container{
  display: flex;
  flex-direction: column;
  gap: 6px;
  padding: 1rem;
}

.db-table {
  border-collapse: collapse;
  width: 100%;
}
  
.db-table td, .db-table th {
  border: 1px solid black;
  text-align: left;
  padding: 1px;
  font-size: 25px;
  text-align: center;
  background-color: #ffffff;
}

.db-table td{
  padding-top: 5px;
}

.date-picker{
  text-align: right;
  padding: 1rem;
  gap: 6px;
  width: 100%;
}

.date-picker input{
  height: 30px;
  width: 200px;
  font-size: 20px;
}